.projectImgContainer{
    width: 50%;
    overflow: hidden;
    margin: 50px;
}
.projectImgContainer img{
    max-width: 100%;
}
@media screen and (max-width:786px){
    .projectImgContainer{
        width: 100%;
        margin: 0;
        padding: 50px;
        box-sizing: border-box;
    }
}